import { useState, useEffect } from 'react'
import { Col, Container, Row } from 'react-bootstrap'
import './Banner.css'

// Assets
import { ArrowRightCircle } from 'react-bootstrap-icons'
import HeaderImg from '../../res/img/Vectary texture.png'

const Banner = () => {
    const [loopNum, setLoopNum] = useState(0);
    const [isDeleting, setIsDeleting] = useState(false);
    const toRotate = ["Web Developer", "Game Programmer", "UI/UX Designer"]
    const [text, setText] = useState('');
    const [delta, setDelta] = useState(300 - Math.random() * 100);
    const period = 2000;

    useEffect(() => {
      let ticker = setInterval(() => {
        tick() 
      }, delta)
    
      return () => {
        clearInterval(ticker)
      }
    }, [text])
    
    const tick = () => {
        let i = loopNum % toRotate.length;
        let fullText = toRotate[i];
        let updatedText = isDeleting ? fullText.substring(0, text.length - 1) : fullText.substring(0, text.length + 1);

        setText(updatedText);
        if (isDeleting) {
            setDelta(prevDelta => prevDelta/2);
        }

        if (!isDeleting && updatedText === fullText) {
            setIsDeleting(true)
            setDelta(period);
        } else if (isDeleting && updatedText === '') {
            setIsDeleting(false);
            setLoopNum(loopNum + 1);
            setDelta(100);
        }
    }

    return (
        <section className="banner" id="home">
            <Container>
                <Row className="align-items-center">
                    <Col xs={12} md={6} xl={7}>
                        <span className="tagline">Welcome Viewers</span>
                        <h1>
                            {`Hi I'm a `}
                            <span className="wrap">{text}</span>
                            <p>Lorem ipsum dolor sit, amet consectetur adipisicing elit. Vitae illo similique eaque. Fugit ipsum voluptatum nobis nostrum voluptatibus dolore provident, maiores non praesentium beatae sequi nemo veritatis quia velit ratione.</p>
                            <button onClick={() => {console.log('connect')}}>Let's Connect <ArrowRightCircle size={25}/></button>
                        </h1>
                    </Col>
                    <Col xs={12} md={6} xl={5} className='phone3D'>
                        <img src={HeaderImg} alt="Header Img"/>
                    </Col>
                </Row>
            </Container> 
        </section>
    )
}

export default Banner